body {
  margin: 0;
}

.search-wrapper {
  width: 100%;
  background-color: #4848fd;
  margin-bottom: 20px;
  padding: 10px;
  position: fixed;
}

.search-form {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.search-form input {
  border: none;
  padding: 10px 20px 10px 5px;
}

.search-form button {
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-top: 80px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  width: 350px;
  box-shadow: 1.95px 1.95px 2.6px rgba(0, 0, 0, .15);
}

.photo-card img {
  height: 250px;
  width: 350px;
}

.photo-card .info {
  max-width: 350px;
  gap: 10px;
  display: flex;
}

.photo-card .info-item {
  text-align: center;
}

.load-more {
  display: none;
}

.show-load-more {
  color: #fff;
  background-color: #5b5bff;
  border: none;
  border-radius: 10px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 40px;
  font-size: 16px;
  display: block;
}

